import React from 'react';
import Col from 'react-bootstrap/esm/Col';
import Row from 'react-bootstrap/esm/Row';
import { Link } from 'react-router-dom';

function TabTopRow(props){
    return(
        <Row style={{marginTop: '13px'}}>
            <Col>
                <Link to={`${props.url}`}><h2 className="tabTitle">{props.title}</h2></Link>
            </Col>
            <Col>
            </Col>
            <Col className="buttonsCol">
                <a href={props.gitHubLink} target="_blank"><h6 className="category projectButtons liveSite">Code</h6></a>
                {props.title !== "Titan" 
                        ? <a href={props.liveSite} target="_blank"><h6 className="category projectButtons code" style={{marginRight: '1em'}}>Live Site</h6></a>
                        : null
                    }
                
            </Col>
        </Row>
    )
}

export default TabTopRow;