import React from 'react';
import Tabs from 'react-bootstrap/Tabs';
import Tab from 'react-bootstrap/Tab';
import Row from 'react-bootstrap/esm/Row';
import Col from 'react-bootstrap/esm/Col';
import 'font-awesome/css/font-awesome.min.css';
import TabTopRow from './TabTopRow';
import { Link } from 'react-router-dom';
import Video from './Video';

function ProjectTabs(props){

    return(
        
        <Tabs defaultActiveKey="home" className='projTab' transition={false} id="noanim-tab-example">
            <Tab eventKey="home" title="Glance">
            <p class="responsive"style={{float: 'right', marginTop:'-4.1em', marginRight:'2em'}}>{props.responsive}</p>
                <Link to={`${props.url}`}><i class="fa fa-expand"></i></Link>
                <TabTopRow title={props.title} liveSite={props.liveSite} gitHubLink={props.gitHubLink} responsive={props.responsive} url={props.url}></TabTopRow>
                <Link to={`${props.url}`}><Row>
                    <Col className="glanceVid" sm={2}>
                        <Link to={`${props.url}`}>
                            <video 
                                 className={props.className} width="400vw" height="250" autoPlay="autoplay" controls muted loop>
                                <source src={props.picUrl} type="video/mp4"></source>
                            </video>
                        </Link>
                    </Col>
                    <Col className="mobVid" sm={4}>
                        {props.title === "Movinest" 
                            ? <Video projHdUrl={props.projHdUrl}/>
                            : (props.title === "Eventfriend"
                                ? <Video projHdUrl={props.projHdUrl}/>
                                : <div></div>)
                        }
                    </Col>
                    <Col className="techCol d-flex justify-content-end">
                        <div className="techStack">
                            <p className="category" style={{lineHeight: '1.6em', padding: '1em'}}>{props.techStack}</p>
                        </div>
                    </Col>
                </Row></Link>
            </Tab>
        </Tabs>
            
    );
}

export default ProjectTabs;