import React from 'react';
import Container from 'react-bootstrap/Container';
import Col from 'react-bootstrap/esm/Col';
import Row from 'react-bootstrap/esm/Row';
import Tab from 'react-bootstrap/esm/Tab';
import Tabs from 'react-bootstrap/esm/Tabs';
import Navbarcomp from './Navbar';
import Eventfriend from '../pages/Eventfriend';
import EventfriendMedia from '../pages/EventfriendMedia';
import Laurier from '../pages/Laurier';
import LaurierMedia from '../pages/LaurierMedia';
import Movinest from '../pages/Movinest';
import MovinestMedia from '../pages/MovinestMedia';
import Titan from '../pages/Titan';
import TitanMedia from '../pages/TitanMedia';
import TabTopRowSinglePage from './TabTopRowSingleProj';

function SingleProjectPageMedia(props){
    
    return(
    <Container className="p-0" fluid={true}>
        <Navbarcomp></Navbarcomp> 
        <Container className="singleProjHeading" style={{marginTop: '6vh'}}>
            <h1 className="anim bigText singleProjHeading" style={{fontSize: '6em'}}>{props.title}</h1>
        </Container>
        <Container className="projectPage anim">
            <Tabs defaultActiveKey="home" className='projTab' transition={false} id="noanim-tab-example">
                <Tab eventKey="home" title="Glance">  
                <p class="responsive" style={{float: 'right', marginTop:'-4.1em', marginRight:'0em'}}>{props.responsive}</p>       
                <TabTopRowSinglePage title={props.title} gitHubLink={props.gitHubLink} responsive={props.responsive} liveSite={props.liveSite}></TabTopRowSinglePage>
                <Row>
                    <Col sm={6}>
                        <video className='projHomeSingle' width="400vw" height="250" autoPlay="autoplay" controls muted loop>
                            <source src={props.picUrl} type="video/mp4"></source>
                        </video>
                    </Col>
                    <Col className="d-flex justify-content-end">
                        <div className="techStackSingle">
                            <p className="category" style={{lineHeight: '1.6em', padding: '1em'}}>{props.techStack}</p>
                        </div>
                    </Col>
                </Row>
                </Tab>
                <Tab eventKey="about" title="Details">
                <p class="responsive" style={{float: 'right', marginTop:'-4.1em', marginRight:'0em'}}>{props.responsive}</p>    
                    <TabTopRowSinglePage title={props.title} gitHubLink={props.gitHubLink} responsive={props.responsive} liveSite={props.liveSite}></TabTopRowSinglePage>
                    {props.title === "Laurier" 
                        ? <Laurier/>
                        : (props.title === "Eventfriend"
                            ? <Eventfriend/>
                            : (props.title === "Titan"
                                ? <Titan/> 
                                : <Movinest/>))
                    }
                    <div style={{marginLeft: 'auto', marginRight:'auto', paddingTop: '2em', textAlign: 'center'}} >
                        <i class="fa fa-chevron-down"></i>
                    </div>
                </Tab>
                
                <Tab eventKey="media" title="Media">
                    <p class="responsive" style={{float: 'right', marginTop:'-4.1em', marginRight:'0em'}}>{props.responsive}</p>       
                    <TabTopRowSinglePage title={props.title} gitHubLink={props.gitHubLink} responsive={props.responsive} liveSite={props.liveSite}></TabTopRowSinglePage>
                    {props.title === "Laurier" 
                            ? <LaurierMedia/>
                            : (props.title === "Eventfriend"
                                ? <EventfriendMedia/>
                                : (props.title === "Titan"
                                    ? <TitanMedia/> 
                                    : <MovinestMedia/>))
                        }
                
                </Tab>
        </Tabs>
        </Container>
    </Container>
        
);
}

export default SingleProjectPageMedia;